.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.movie-list {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.movie-card {
  width: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.movie-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.movie-card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.movie-info {
  padding: 15px;
}

.movie-info h2 {
  color: #333;
  margin: 0;
  font-size: 1.8em;
}

.movie-info p {
  color: #777;
  margin: 5px 0;
  font-size: 1.2em;
}

